						<center><p style="font-size: 0.8em;color: #333;margin-bottom: 0.5em;">會員中心</p></center>
					</div>
				</a>
			</div>
		</div>
	</div>
</template>

<script>
	import {getService} from '../../assets/js/configs.js'
export default {
  name: 'index',
  data(){
  	return{
  		imgUrl:'',
  		itemInfo:''
  	}
  },
  mounted:function () {
  	this.getImg();
  	this.getItemInfo();
  },
  methods:{
  	getItemInfo:function () {
  		var param = {
  			page:1,
  			unit:40
  		}
  		$.ajax({
  			type:'post',
  			url:getService()+"/adv/getHomepageItems",
  			dataType: "json",
  			data:JSON.stringify(param),
  			async:true,
			headers: {'Content-Type': 'application/json'},
  			xhrFields: {
		        withCredentials: true
		    },
		    crossDomain: true,
		    success:function (res) {
		    	if (res.error_code == 0) {
		    		this.itemInfo = res.data;
		    	}
		    }.bind(this)
  		});
  	},
  	getImg:function () {
  		$.ajax({
  			type:'get',
  			url:getService()+"/adv/getHomepage",
  			dataType: "json",
  			async:true,
			headers: {'Content-Type': 'application/json'},
  			xhrFields: {
		        withCredentials: true
		    },
		    crossDomain: true,
		    success:function (res) {
		    	if (res.error_code == 0) {
					this.imgUrl = res.data.imageLink;
		    	}
		    }.bind(this)
  		});
  	}
  }
}
</script>

<style>
	@import url("../../assets/css/PCstyle.css");
</style>


